export default `
<script>
{
    //join() 方法把数组各元素拼接成字符串
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    write('默认使用"，"进行分隔', fruits.join())
    write('else使用""进行分隔', fruits.join(''))
    write('else使用" "进行分隔', fruits.join(' '))
}
</script>
<br>
<script>
{
    //toString() 方法可把数组转换为字符串，并返回结果
    //元素之间用逗号'，'进行分隔
    let fruits = ["Banana", "Orange", "Apple", "Mango"]
    write(fruits.toString())
}
</script>
`



